import "./VerbTable.css";
import React from "react";

export interface VerbTense {
  header: string;
  conjugations: string[];
}

export interface VerbTableProps {
  title: string;
  verbs: VerbTense[];
  row_labels?: string[];
}

export function VerbTable({ title, verbs, row_labels }: VerbTableProps) {
  const leftCol: VerbTense = {
    header: "",
    conjugations: row_labels || ["yo", "tu", "ello/a", "nosotros", "ellos"],
  };
  verbs = [leftCol, ...verbs];
  const headers = verbs.map((verb) => verb.header);
  let rows: string[][] = verbs[0].conjugations.map(() => []);
  verbs.forEach((verb) => {
    verb.conjugations.forEach((conjugation, i) => {
      rows[i].push(conjugation);
    });
  });

  return (
    <details className={"verb-collapsable"}>
      <summary>{title}</summary>

      <table className={"verb-table"}>
        <tbody>
          <tr>
            {headers.map((header) => (
              <th key={header}>{header}</th>
            ))}
          </tr>
          {rows.map((row, rowIdx) => (
            <tr key={rowIdx}>
              {row.map((conjugation, idx) => (
                <td key={`${headers[idx]}-${conjugation}`}>{conjugation}</td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </details>
  );
}
