import sqlite3InitModule, {
  type DatabaseApi,
  type Sqlite3Static,
} from "@sqlite.org/sqlite-wasm";
import { VerbTense } from "./components/VerbTable";
import { ALL_CONJUGATION_PREFIXES, Verb } from "./App";
import verbSql from "./verbDatabase.sql";

interface Sqlite3Empty {
  state: "empty";
}
interface Sqlite3Loading {
  state: "loading";
  promise: Promise<Sqlite3Static>;
}
interface Sqlite3Loaded {
  state: "loaded";
  sqlite3: Sqlite3Static;
}

var SQLITE3_GLOBAL: Sqlite3Empty | Sqlite3Loading | Sqlite3Loaded = {
  state: "empty",
};

export class VerbDatabase {
  private readonly database: DatabaseApi;

  constructor(database: DatabaseApi) {
    this.database = database;
  }

  static async downloadAndInit(): Promise<VerbDatabase> {
    let sqlite3;
    if (SQLITE3_GLOBAL.state === "empty") {
      const promise = sqlite3InitModule();
      SQLITE3_GLOBAL = { state: "loading", promise };
      sqlite3 = await promise;
      SQLITE3_GLOBAL = { state: "loaded", sqlite3 };
    } else if (SQLITE3_GLOBAL.state === "loading") {
      sqlite3 = await SQLITE3_GLOBAL.promise;
    } else {
      sqlite3 = SQLITE3_GLOBAL.sqlite3;
    }

    const response = await fetch(verbSql);
    const buffer = await response.arrayBuffer();
    const p = sqlite3.wasm.allocFromTypedArray(buffer);
    const db: DatabaseApi = new sqlite3.oo1.DB();
    const rc = sqlite3.capi.sqlite3_deserialize(
      db.pointer,
      "main",
      p,
      buffer.byteLength,
      buffer.byteLength,
      sqlite3.capi.SQLITE_DESERIALIZE_FREEONCLOSE,
      // Optionally:
      // | sqlite3.capi.SQLITE_DESERIALIZE_RESIZEABLE
    );
    db.checkRc(rc);

    return new VerbDatabase(db);
  }

  private extractTenseFromRow(
    row: { [_: string]: string },
    infinitive: string,
    prefix: string,
  ): VerbTense {
    return {
      header: infinitive,
      conjugations: [
        row[`${prefix}_first_person_singular`] || "",
        row[`${prefix}_second_person_singular`] || "",
        row[`${prefix}_third_person_singular`] || "",
        row[`${prefix}_first_person_plural`] || "",
        row[`${prefix}_third_person_plural`] || "",
      ],
    };
  }

  getConjugationsForVerb(verbInfinitive: string): Verb | null {
    verbInfinitive = verbInfinitive.toLowerCase().trim();
    const row = this.database.selectObject(
      "SELECT * FROM conjugations WHERE infinitive = ?",
      [verbInfinitive],
    );

    if (row == null) {
      return null;
    }
    const infinitive = row["infinitive"];
    if (infinitive == null) {
      return null;
    }
    const gerund = row["gerund"];
    const pastParticiple = row["past_participle_masculine_singular"];

    const tenses = ALL_CONJUGATION_PREFIXES.map((prefix) =>
      this.extractTenseFromRow(row, infinitive, prefix[0]),
    );
    return { infinitive, gerund, pastParticiple, tenses };
  }
}
