import React, {
  FormEventHandler,
  MouseEventHandler,
  useEffect,
  useRef,
  useState,
} from "react";
import "./App.css";
import { VerbTable, VerbTableProps, VerbTense } from "./components/VerbTable";
import {
  DEFAULT_INFINITIVE_GERUND_PARTICIPLE_TENSES,
  DEFAULT_TENSE_TABLES,
} from "./defaultData";
import { VerbDatabase } from "./verbDatabase";
import { multi } from "itertools-ts";

export const ALL_CONJUGATION_PREFIXES = [
  ["indicative_present", "Present"],
  ["indicative_preterite", "Past Preterite"],
  ["indicative_imperfect", "Past Imperfect"],
  ["indicative_future", "Future"],
  ["indicative_conditional", "Conditional"],
  ["subjunctive_present", "Subjunctive Present"],
  ["subjunctive_imperfect", "Subjunctive Past"],
  ["imperative", "Imperative"],
];

export interface Verb {
  infinitive: string;
  gerund: string;
  pastParticiple: string;
  tenses: VerbTense[];
}

interface VerbState {
  verbs: Verb[];
}

function renderSelectedVerbs(
  verbState: VerbState | null,
  removeVerbCallback: (e: React.MouseEvent, index: number) => void,
) {
  if (verbState == null) {
    return (
      <div className="selected-verbs-default">
        No verb selected, showing generic patterns
      </div>
    );
  }

  return (
    <div className="selected-verbs-list">
      {verbState.verbs.map((verb, idx) => {
        return (
          <div key={verb.infinitive} className="selected-verbs-entry">
            <button type={"button"} onClick={(e) => removeVerbCallback(e, idx)}>
              ❌
            </button>
            {verb.infinitive}
          </div>
        );
      })}
    </div>
  );
}

function App() {
  const [verbQuery, setVerbQuery] = useState<string | null>(null);
  const [verbState, setVerbState] = useState<VerbState | null>(null);
  const [verbDatabase, setVerbDatabase] = useState<VerbDatabase | null>(null);
  useEffect(() => {
    (async () => {
      const verbDatabase = await VerbDatabase.downloadAndInit();
      setVerbDatabase(verbDatabase);
    })();
  }, []);
  const verbInputRef = useRef<HTMLInputElement | null>(null);

  useEffect(() => {
    if (verbQuery === null || verbDatabase === null) {
      return;
    }

    const existingVerbs = verbState?.verbs || [];

    const newVerb = verbDatabase.getConjugationsForVerb(verbQuery);
    if (newVerb === null) {
      return;
    }
    // Don't double add
    if (
      existingVerbs.find(
        (element) => element.infinitive === newVerb.infinitive,
      ) != null
    ) {
      return;
    }

    setVerbState({
      verbs: [...existingVerbs, newVerb],
    });
    setVerbQuery(null);
  }, [verbQuery, verbDatabase, verbState]);

  let initialTableProp: VerbTableProps = {
    title: "Infinitive, Gerund, Past Participle",
    row_labels: ["infinitive", "gerund", "past participle"],
    // Default data, overridden if we have something
    verbs: DEFAULT_INFINITIVE_GERUND_PARTICIPLE_TENSES,
  };
  let tableProps: VerbTableProps[];
  if (verbState == null) {
    tableProps = DEFAULT_TENSE_TABLES;
  } else {
    const verbs = verbState.verbs;

    initialTableProp.verbs = verbs.map((verb) => {
      return {
        header: verb.infinitive,
        conjugations: [verb.infinitive, verb.gerund, verb.pastParticiple],
      };
    });

    let tensesByVerb = verbs.map(({ tenses }) => tenses);
    const tensesByTense = multi.zip(...tensesByVerb);
    const tensesWithName = multi.zip(ALL_CONJUGATION_PREFIXES, tensesByTense);
    tableProps = Array.from(tensesWithName).map(
      ([tenseName, tenses]): VerbTableProps => {
        return {
          title: tenseName[1],
          verbs: tenses,
        };
      },
    );
  }

  const handleSubmit: FormEventHandler = (e) => {
    e.preventDefault();
    const current = verbInputRef.current;
    if (current == null) {
      return;
    }
    setVerbQuery(current.value);
    current.value = "";
  };

  const handleClear = (e: React.MouseEvent, idx: number) => {
    e.preventDefault();
    if (verbState == null) {
      // SHOULDN'T HAPPEN!
      return;
    }

    const verbs = [...verbState.verbs];
    verbs.splice(idx, 1);
    if (verbs.length > 0) {
      setVerbState({ verbs });
    } else {
      setVerbState(null);
    }
  };

  return (
    <div className="App">
      <form className="verb-form" onSubmit={handleSubmit}>
        <div className="search-box">
          <input
            className="search-box-text"
            type={"search"}
            placeholder={"Verb"}
            ref={verbInputRef}
          />
          <input className="search-box-button" type={"submit"} value={"➕"} />
        </div>
      </form>
      {renderSelectedVerbs(verbState, handleClear)}
      <div className="tables-container">
        <VerbTable {...initialTableProp} />
        {tableProps.map((table) => (
          <VerbTable key={table.title} {...table} />
        ))}
      </div>
    </div>
  );
}

export default App;
